.main-navigation {
	clear: both;
	display: block;
	background-color: black;
	width: 100%;
	padding: 0.5rem 3rem;

	#primary-menu {
		padding-bottom: 8px;
	}

	#menu-undermeny {
		padding-top: 8px;
		margin: 0 auto;
		border-top: 1px solid #d3d3d3;
	}

	ul {
		display: flex;
		justify-content: space-around;
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			position: absolute;
			top: 100%;
			left: -999em;
			z-index: 99999;
			background-color: black;
			display: flex;
			flex-direction: column;

			ul {
				left: -999em;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
				padding: 0 0.5rem;
				margin: 0.5rem;
		}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	}

	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: none;
}

